import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { AppConfig } from '../../app/app.config';
import { ForgotPasswordPage } from '../forgot-password/forgot-password';

import { TabsPage } from '../tabs/tabs';

/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  public email:string = 'test@gmail.com';
  public password:string = 'test';

  public loginFail: boolean = false;
  public loginFailMsg = 'Email or Password is not correct!';

  private loginUrl = AppConfig.getUrl() + '/api/login';

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LoginPage');
  }

  onLoginButtonClick() {
    if (this.email.trim() == '' || this.password.trim() == '') {
      return false;
    }

    this.navCtrl.push(TabsPage, { displayWelcome: false});
  }

  onClickForgotButton() {
    this.navCtrl.push(ForgotPasswordPage);
  }

}



